import React , {Component}  from 'react';
import {CSSTransition} from 'react-transition-group';
import './App.css';
class App extends Component
{
    constructor(props){
        super(props);
        
        this.state ={
          data : [],
          show:false,
          showThis:0,
          doctor:{}
        }

      }
componentWillMount() {
    fetch("https://www.mocky.io/v2/5d70aa0c3300006c0077932b").then(res=>res.json())
    .then(doctor=>{
        this.setState({
            doctor:doctor
        })
    })
    fetch("https://www.mocky.io/v2/5d7378d533000000040817e3").then(res=>res.json())
    .then(interview=>{
        let push=this.state.data;
        interview.forEach(link => {
            fetch('https://www.googleapis.com/youtube/v3/videos?part=snippet&id='+link+'&key=AIzaSyBG-b1BeuadWaDPuSfBZca64bHuyX6GTd0')
    .then(res => res.json())
          .then((data) => {
              data.items=data.items[0];
              push.push(data);
              this.setState({
                  data:push
              });
          });
        });
    });
}
toggle(data=null){
    if(this.state.show)
    this.setState({show:false});
    else 
    this.setState({show:true,showThis:data});
    
}
render(){
    console.log()
    return(<div className="container">
        
        {
            this.state.show?
            <CSSTransition
	            in={true}
	            appear={true}
	            timeout={500}
	            classNames="pull">
                    <div  style={{position:"fixed",top:"0px",left:"0px",backgroundColor:"rgba(0,0,0,0.5)",width:"100%",height:"100%",zIndex:"9999999"}}>
                        <div style={{position:"fixed",bottom:"10px",left:"0px",zIndex:"99",backgroundColor:"#efdaf7",padding:"5px"}}>
                            <span style={{display:"inline-block",color:"#0004ff"}}>Was this helpful?
                                <br/>Let us know!!
                            </span>
                            <div className="btn" style={{backgroundColor:"green",color:"white",border:"solid",borderColor:"white",borderRadius:"20px 20px 20px 20px",display:"inline-block"}}><i className="far fa-thumbs-up"></i></div>
                            <div className="btn" style={{backgroundColor:"red",color:"white",border:"solid",borderColor:"white",borderRadius:"20px 20px 20px 20px",display:"inline-block"}}><i className="far fa-thumbs-down"></i></div>
                        </div>
                        <span  onClick={()=>{this.toggle(null)}} style={{"float": "right",position:"absolute",top:"10px",right:"0px"}} className="far fa-window-close btn"></span>
                        <div onClick={()=>{}} className="thumbnail"style={{padding:"10px",margin:"0px",overflow:"auto"}}>
                            <div className="col-md-7"><iframe title={this.state.showThis.items.snippet.thumbnails.high.url} style={{width:"100%",height:window.screen.width>1080?"400px":window.screen.width>720?"350px":window.screen.width>420?"190px":"150px"}} src={"https://www.youtube.com/embed/"+this.state.showThis.items.id} alt="thumbnail" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe><br/></div>
                            <div className="col-md-5">
                                <div className="col-md-12 col-sm-6"><img src={this.state.doctor.doc_img_url} alt="doctor" style={{width:"100%",height:"auto"}}/></div>
                                <div className="col-md-12 col-sm-6">
                                <h2 className="txt" style={{marginBottom:"5px",fontSize:"25px"}}>{this.state.doctor.doc_firstname} {this.state.doctor.doc_middlename} {this.state.doctor.doc_lastname}</h2>
                                <h6 className="txt" style={{marginBottom:"5px",fontSize:"20px"}}>
                                <span style={{"color":"#1DA6FD"}}>Designation : {this.state.doctor.designation}</span>
                                </h6>
                                <div className="row">
                                    {
                                    this.state.doctor.specialisation.map(skill=>
                                        <span key={skill} className="skill">{skill}</span>)
                                    }
                                </div>
                                <h6 className="txt" style={{marginBottom:"5px",fontSize:"20px",marginTop:"5px"}}>
                                <span style={{"color":"#1DA6FD"}}>City : {this.state.doctor.city}</span>
                                </h6></div>
                            </div>
                            <br/>
                            <div className="col-xs-12" style={{marginTop:"10px"}}>
                            <h3 style={{fontSize:"25px",fontWeight:"bold"}}>{this.state.showThis.items.snippet.title}</h3>
                            <textarea id="des" style={{width:"100%",height:"300px",color:"black",fontWeight:"500",overflow:"auto",border:"none",backgroundColor:"#f4f2f7"}} readOnly>{this.state.showThis.items.snippet.localized.description}</textarea></div>
                        </div>
                    </div>
            </CSSTransition>
            :this.state.data.map((data)=>
                <div key={data.id} className="col-xs-12 col-sm-6 col-md-4 cont">
                    <div onClick={()=>{this.toggle(data)}} className="thumbnail"style={{padding:"10px"}}>
                        <img src={data.items.snippet.thumbnails.high.url} style={{width:"100%",height:"auto"}} alt="thumbnail"/><br/>
                        <h6>{data.items.snippet.title}</h6>
                    </div>
                </div>)
        }
    </div>);
}
}


export default App;